.form-conteiner {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  padding-top: -10px;

  .form-content {
    margin-top: -5rem;
  }

  .mask {
    width: 100%;
    height: 70px;
    position: absolute;
    left: 0;
